import React from "react";
import Slide from "@material-ui/core/Slide";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  grass: {
    backgroundImage: "linear-gradient(-175deg, #d2db8f 0%, #8fa368 100%)",
    height: "25vh"
  },
  road: {
    backgroundImage: "linear-gradient(-180deg, #c1c2b6 0%, #a5a69a 70%, #a5a69a 100%)",
    height: "5vh"
  }
}));

export default function Road() {
  const classes = useStyles();
  return (
    <div>
      <Slide
        direction="up"
        in={true}
        timeout={{ enter: 1000, exit: 500 }}
        mountOnEnter
        unmountOnExit
      >
        <div>
          <div className={classes.road}></div>
          <div className={classes.grass}></div>
        </div>
      </Slide>
    </div>
  );
}
