import React from "react";
import { useSpring, animated } from "react-spring";
import { ReactComponent as Logo } from "./assets/art/svg/logo/Catalyst-Logo_group.svg";
import { makeStyles } from "@material-ui/core/styles";
const useStyles = makeStyles((theme) => ({
  logo: {
    position: "absolute",
    borderRadius: "5px",
    willChange: "transform",
    minWidth: "40ch",
    minHeight: "40ch",
    width: "20vw",
    height: "20vw",
    maxWidth: "100ch",
    maxHeight: "100ch"
  },
  container: {
    width: "100%",
    height: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  }
}));
const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2];
const trans = (x, y) => `translate3d(${x / 5}px,${y / 5}px,0)`;

export default function LogoGroup() {
  const classes = useStyles();
  const [props, set] = useSpring(() => ({
    xy: [0, 0],
    config: { mass: 10, tension: 550, friction: 140 }
  }));
  return (
    <div className={classes.container} onMouseMove={({ clientX: x, clientY: y }) => set({ xy: calc(x, y) })}>
      <animated.div
        className={classes.logo}
        style={{ transform: props.xy.interpolate(trans) }}
      >
        <Logo />
      </animated.div>
    </div>
  );
}
