import React from "react";
import Slide from "@material-ui/core/Slide";
import { makeStyles } from "@material-ui/core/styles";
import posed from "react-pose";
import LogoGroup from "./logo.js";
import "./index.css";

const useStyles = makeStyles((theme) => ({
  skyBox: {
    position: "relative"
  },
  sky: {
    backgroundImage: "linear-gradient(-180deg, #5fb6d9 0%, #ededdf 100%)",
    height: "70vh"
  },
  houseBox: {
    position: "absolute",
    bottom: 0,
    width: "100vw",
    padding: "0 auto"
  },
  house: {
    margin: "0 auto",
    width: 200,
    height: 250,
    background: "#fff"
  },
  logoDiv: {
    position: "absolute",
    top: -100,
    margin: "0 auto",
    height: 100,
    width: "100vw"
  },
  logo: {
    zIndex: 9999,
    position: "absolute",
    top: "-100",
    margin: "0 auto",
    height: 200,
    width: "100vw"
  }
}));
const Box = posed.div({
  hoverable: true,
  pressable: true,
  init: {
    scale: 1,
    boxShadow: "0px 0px 0px rgba(0,0,0,0)"
  },
  hover: {
    scale: 1.2,
    boxShadow: "0px 5px 10px rgba(0,0,0,0.2)"
  },
  press: {
    scale: 1.1,
    boxShadow: "0px 2px 5px rgba(0,0,0,0.1)"
  }
});

export default function Sky() {
  const classes = useStyles();
  const House = () => <Box className={classes.house} />;
  return (
    <div className={classes.skyBox}>
      <div>
        <Slide
          direction="down"
          in={true}
          timeout={{ enter: 1000, exit: 500 }}
          mountOnEnter
          unmountOnExit
        >
          <div>
            <div className={classes.sky}></div>
          </div>
        </Slide>
      </div>
      <div className={classes.houseBox}>
        <div className={classes.logoDiv}>
          <LogoGroup className={classes.logo} />
        </div>
        <Slide
          direction="down"
          in={true}
          timeout={{ enter: 1800, exit: 500 }}
          mountOnEnter
          unmountOnExit
        >
          <div>
            <House />
          </div>
        </Slide>
      </div>
    </div>
  );
}
